import { render } from '@testing-library/react';
import React from 'react'

export default function ListItem(props) {
    const data = props.data;
    const date = data.createdAt;
    console.log(date);
    console.log(data);
        return (
            <div>
                {data.map(details => (
                <ul key={details.id}>
                    <li>
                            {new Date(details.createdAt.toString()).getMonth() + '/'
                                + new Date(details.createdAt.toString()).getUTCDate() + '/' +
                                new Date(details.createdAt.toString()).getFullYear()
                            }
                        </li>
                        <li>
                        {details.enough_sleep.toString()}
                    </li>
                        <li>
                        {details.wake_up}
                        </li>
                        <li>
                        {details.medicine.toString()}
                    </li>
                    <li>
                        {details.diet.toString()}
                    </li>
                    <li>
                        {details.excercise.toString()}
                    </li>
                        <li>
                        {details.changes}
                    </li>
                    <li>
                        {details.comments}
                        </li>
                        <li>
                        {details.good_day.toString()}
                    </li>
                </ul>
                ))}
            </div>
        );
     
}