import React, {
    useState,
    useEffect
} from 'react';
import logo from '../logo.svg';
import ListItem from '../components/ListItem';
/*
changes: "I would like to eat better"
comments: "Overall I had a great day"
createdAt: "2021-05-14T02:23:09.459Z"
diet: false
enough_sleep: true
excercise: true
good_day: true
id: "609ddf0d9ee9c62f75d0fdef"
medicine: true
updatedAt: "2021-05-14T02:23:09.459Z"
wake_up: "7:30"
*/ 
export default function FetchDayDetails() {
    const [data, dataSet] = useState([{
        changes: '',
        comments: '',
        createdAt: '',
        diet: false,
        enough_sleep: false,
        excercise: false,
        good_day: false,
        id: '',
        medicine: false,
        updatedAt: '',
        wake_up: '',
    }]);
    const [loading, loadingSet] = useState(true);

    useEffect(() => {
        async function fetchMyAPI() {
            let response = await fetch('http://highscore-api.herokuapp.com/day-details')
            //let response = await fetch('http://localhost:9000/day-details');
            response = await response.json();
            console.log("Fetch Api"); console.log(response);
            dataSet(response);
            loadingSet(false);
        }

        fetchMyAPI()
    }, [])
    
    if (loading) {
        return (
            <div>
                <img src={logo} style={{ height: '100%', width: '100%'}} alt="logo" />
                <h2>Loading...</h2>
            </div>
          );
    } else {
        return (<ListItem data={data}/>);
    }

    
}